/* 전체 앱 컨테이너 */
.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  color: #fff;
}

/* 배경화면 */
body {
  background: linear-gradient(135deg, #1a1a2e, #16213e, #0f3460);
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

/* 카테고리 그리드 - 8행 5열로 배치 */
.category-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);  /* 5열 */
  grid-template-rows: repeat(8, auto);    /* 8행 */
  gap: 12px;
  justify-content: center;
}

/* 카테고리 버튼 디자인 */
.category-button {
  padding: 14px 18px;
  background-color: #0f3460;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  min-width: 130px;
  min-height: 50px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.category-button:hover {
  background-color: #53354a;
}

.category-button.selected {
  background-color: #e94560;
}

/* 질문 박스 */
.question-box {
  background-color: #16213e;
  padding: 20px;
  margin: 20px 0;
  border-radius: 8px;
  border: 1px solid #0f3460;
  text-align: center;
}

/* 버튼 공통 스타일 */
.start-button, .record-button, .control-button {
  margin: 10px;
  padding: 15px 30px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  background-color: #16213e;
  color: #fff;
  font-size: 1.1rem;
}

.start-button:hover,
.record-button:hover,
.control-button:hover {
  background-color: #e94560;
}

/* 버튼을 한 행에 배치 */
.button-group, .question-section {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

/* 피드백 박스 */
.feedback-box {
  background-color: #0f3460;
  padding: 15px;
  margin-top: 10px;
  border-radius: 5px;
  border-left: 4px solid #e94560;
  text-align: center;
}

/* 피드백 텍스트 */
.feedback-text {
  color: #fff;
  font-weight: bold;
}

/* 탭 메뉴 스타일 */
.tab-menu {
  display: flex;
  margin-bottom: 20px;
  border-bottom: 1px solid #e94560;
}

.tab-menu button {
  padding: 10px 20px;
  background-color: transparent;
  color: #fff;
  border: none;
  border-bottom: 3px solid transparent;
  cursor: pointer;
}

.tab-menu button.active-tab {
  color: #e94560;
  border-bottom: 3px solid #e94560;
}

/* API 키 섹션 스타일 */
.api-key-section {
  margin-bottom: 20px;
  display: flex;
  gap: 10px;
}

.api-key-section input {
  flex: 1;
  padding: 12px;
  border: 1px solid #0f3460;
  border-radius: 8px;
  background-color: #16213e;
  color: #fff;
}

/* 번역 섹션 스타일 */
.translation {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px dashed #0f3460;
}

.translation h4 {
  color: #e94560;
  margin: 0 0 5px 0;
}

/* 실시간 자막 스타일 */
.subtitle {
  background-color: #16213e;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  border-left: 5px solid #e94560;
}

.subtitle h3 {
  margin-top: 0;
  color: #fff;
}

/* 오디오 플레이어 스타일 */
.audio-player {
  margin-bottom: 20px;
}

.audio-player audio {
  width: 100%;
  margin-bottom: 10px;
  background-color: #16213e;
  border-radius: 8px;
}

.speed-control {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
  justify-content: center;
}

.speed-control button {
  background-color: #16213e;
  color: #fff;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.speed-control button:hover {
  background-color: #e94560;
}

/* 피드백 섹션 스타일 */
.feedback-section {
  background-color: #16213e;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  border-left: 5px solid #e94560;
}

.feedback-button {
  background-color: #e94560;
  padding: 10px 20px;
  margin: 10px 0;
  border: none;
  border-radius: 8px;
  color: #fff;
  cursor: pointer;
}

.feedback-button:disabled {
  background-color: #545454;
  cursor: not-allowed;
}

.feedback-content {
  white-space: pre-line;
}

/* 히스토리 섹션 스타일 */
.history-controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  justify-content: center;
}

.export-button {
  background-color: #0f3460;
}

.export-button:hover {
  background-color: #e94560;
}

.clear-button {
  background-color: #e94560;
}

.no-history {
  text-align: center;
  color: #fff;
  margin: 40px 0;
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.history-item {
  background-color: #16213e;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
}

.history-date {
  color: #e94560;
  font-size: 0.9em;
  margin-top: -10px;
}

.history-question, .history-answer, .history-feedback {
  margin-top: 15px;
  padding: 10px;
  border-radius: 5px;
}

.history-question {
  background-color: rgba(15, 52, 96, 0.5);
}

.history-answer {
  background-color: rgba(22, 33, 62, 0.5);
}

.history-feedback {
  background-color: rgba(233, 69, 96, 0.2);
}

.history-question h4, .history-answer h4, .history-feedback h4 {
  margin-top: 0;
  color: #e94560;
}